.words-area {
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
}

.words {
  display: flex;
  flex-wrap: wrap;
  width: 80%;
  position: relative;
  margin-top: 1em;
}

.word {
  background-color: whitesmoke;
  color: #333;
  border-radius: 1em;
  font-size: 1em;
  width: max-content;
  height: 1em;
  border: none;
  letter-spacing: 0.01em;
  margin: .5em;
  padding: 1em;
  text-transform: uppercase;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  box-sizing: content-box;
}

.word:hover {
  cursor: pointer;
  transform: scale(1.09) perspective(1px);
  z-index: 1;
}

.panagram {
  background-color: #F2DB50;
  color: black;
  font-weight: 500;
}

@media screen and (max-width: 800px) {
  .words {
    flex-wrap: nowrap;
    overflow-x: scroll;
    overflow-y: hidden;
    border-radius: 5px;
    margin-bottom: 0em;
    position: relative;
    width: 100%;
  }

  .word {
    width: 80vw;
    position: relative;
  }
}